
.settings_menu , .settings_container > .inner_container{
    box-shadow: 0 .375rem .75rem rgba(140,152,164,.075);
    border: 1px solid #e7eaf3b3;
    border-radius: 8px;
    border-collapse: collapse
}

.settings_menu{
    position: sticky;
    top: 90px;
    height: fit-content;
}

.settings_menu .menu_title{
    background: #6587dc;
    color: #fff;
    font-size: 12px;
    padding: 8px;
    border-radius: 8px 8px 0 0;
}

.settings_menu .menu_links{
    overflow: auto;
    max-height: calc(100vh - 150px);
}

.settings_menu .menu_link{
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #e8e8e8;
    display: flex;
    justify-content: space-between;
    padding: 8px;
    transition: .1s ease-in;
}

.settings_menu .menu_link:last-child{
    border-radius:0 0 8px 8px;
}

.settings_menu .menu_link > span{
    color: #343a40;
    font-size: 13px;
    font-weight: 500;
}

.settings_menu .menu_link:hover, .settings_menu .menu_link.active{
    background: #e0f3ff;
}

.settings_menu .menu_link:hover span, .settings_menu .menu_link.active span{
    color: #6587dc;
}

.settings_title {
    font-weight: 600;
    font-size: 18px;
    color: #343a40;
    margin-bottom: 10px;
    padding-bottom: 20px;
    border-bottom: 1px solid #e8e8e8;
}