.header_rigth_menu {
    position: relative;
}

.header_rigth_menu .menu_icon{
    padding: .25rem;
    border-radius: 50%;
}

.header_rigth_menu .menu_icon:hover{
    background: #e5eef9b6;
}

.header_rigth_menu:has(.inner_menu.active) .menu_icon{
    background: #ddeaf9;
}

.header_rigth_menu > .inner_menu{
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 100%;
    right: 0;
    width: 280px;
    background: #f7f9fd;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
    padding: 7px 0;
    transition: opacity .1s ease-in;
}

.header_rigth_menu > .inner_menu.active{
    opacity: 1;
    visibility: visible;
}


.header_rigth_menu > .inner_menu > .menu_option {
    width: 100%;
    color: #4a4a4a;
    font-size: 12px;
    padding: 5px 22px;
    display: flex;
    gap: 14px;
    align-items: center;
    user-select: none;
}

.header_rigth_menu > .inner_menu > .menu_option:hover {
    background: #ebedf0;
}

.header_rigth_menu > .inner_menu > .menu_option > img {
    filter: brightness(0) saturate(100%) invert(30%) sepia(3%) saturate(12%) hue-rotate(11deg) brightness(94%) contrast(97%);
}