@tailwind base;
@tailwind components;
@tailwind utilities;
@import url(https://fonts.googleapis.com/css?family=Inter:100,200,300,regular,500,600,700,800,900);

*{
  margin: 0;padding: 0;
  box-sizing: border-box;
  font-family:  'Inter', sans-serif;
}

body {
  background: #fafafa;
}

body:has(#modal){
  overflow: hidden;
  /* position: fixed; */
}


*::-webkit-scrollbar {
  width: 8px; 
  height: 8px; 
}

*::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

*::-webkit-scrollbar-thumb {
  background: #e5e7eb; 
}

*::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.enter_bg{
  height: 100vh;
  width: 100%;
  background: linear-gradient(-45deg, #6587dc, #8aaaff, #4c6bbd);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}


@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}


.btn{
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 9px 12px;
  color: #FAFAFA;
  font-size: 13px;
  font-weight: 400;
  background: #6587dc;
  border-radius: 6px;
}

.btn:disabled{
  background: #a9a9a9 !important;
}

.btn.secondary{
  background: unset;
  border: 1px solid #6587dc;
  color: #6587dc;
}

.btn.add{
  background: #65dc87;
}


.modal_container {
  position: fixed;
  inset: 0; 
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  z-index: 999;
  padding: 40px 20px 20px;
  animation: fade .1s ease-in;
  overscroll-behavior: contain;
}

.modal {
  max-width: 100%;
  min-width: 320px;
  border-radius: 8px;
  background-color: #ffffff;
  border: 1px solid #e7eaf3b3;
  -moz-box-shadow: 0 .375rem .75rem rgba(140,152,164,.075);
  -webkit-box-shadow: 0 .375rem .75rem rgba(140,152,164,.075);
  box-shadow: 0 .375rem .75rem rgba(140,152,164,.075);
  border-collapse: separate;
}

.modal .modal_header{
  border-bottom: 1px solid #e7eaf3b3;
  position: relative;
  padding:16px;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: space-between;
}

.modal .modal_header > h2{
  font-weight: 600;
  line-height: 1.2;
  font-size: 15px;
  color: #343a40;
}

.modal .modal_content{
  padding: 15px;
}


