@keyframes fade{
  from {
      opacity: 0;
      transform: translateY(-10px);
  }
  to{
      opacity: 1;
      transform: translateY(0px);
  }
} 

.main_container{
  margin-top: 60px;
  flex: 1;
  z-index: 8;
  position: relative;
}

.main_outer{
    padding-left: 200px;
    flex-direction: column;
    display: flex;
    flex: 1;
}

.main_inner{
  padding: 30px 30px 0;
  flex: 1;
}

.primary_table{
  width: 100%;
  border: 1px solid #e7eaf3b3;
  table-layout: fixed;
}

.primary_table > thead{
  background: #f8fafd;
}

.primary_table > thead td {
  font-weight: 500;
  font-size: 11px;
  color: #677788;
  vertical-align: sub;
  text-transform: uppercase;
  border-bottom: 1px solid #ebedec;
  border-right: 1px solid #ebedec;
}

.primary_table td{
  padding: 10px 12px;
}

.primary_table .action_column:has(input[type='checkbox']){
  text-align: center;
}

.primary_table .action_column> input[type='checkbox']{
  vertical-align: middle;
}

.primary_table > tbody td{
  border-right: 1px solid #ebedec;
  font-size: 12px;
  line-height: 16px;
  color: #343a40;
}

.primary_table > thead td:last-child,.primary_table > tbody td:last-child{
  border-right: unset;
}

.primary_table tbody tr{
  border-bottom: 1px solid #ebedec;
}

.primary_table tbody tr:last-child{
  border-bottom: unset;
}

.primary_table tbody tr:nth-child(even){
  background: #f8fafd;
}

input[type=checkbox]{
  accent-color:#6587dc;  
  cursor: pointer;
}

.primary_input {
  display: block;
  width: 100%;
  padding: .6125rem 1rem;
  font-size: .875rem;
  font-weight: 400;
  line-height: 1.5;
  color: #333538;
  background-color: #fff;
  background-clip: padding-box;
  border: .0625rem solid rgba(231, 234, 243, .7);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: .3125rem;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  user-select: none;
}

.primary_input:focus {
  color: #1e2022;
  background-color: #fff;
  border-color: rgba(140, 152, 164, .25);
  outline: 0;
  box-shadow: 0 0 1rem 0 rgba(140, 152, 164, .25);
}

.primary_select{
  position: relative;
  user-select: none;
  min-width: 150px;
}

.primary_select .select_top{
  height: 42px;
  cursor: pointer;
  background: #FFFFFF;
  border: .0625rem solid rgba(231, 234, 243, .7);
  border-radius: .3125rem;
  padding: 0 12px;
  font-size: .875rem;
  font-weight: 400;
  display: flex;
  gap: 10px;
  justify-content: space-between;
  align-items: center;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.primary_select .select_top .selected_value{
  position: absolute;
  width: calc(100% - 60px);
  white-space: nowrap;
  overflow: scroll;
  color: #3D3D3B;
  text-transform: capitalize;
}

.primary_select .select_top img{
  margin-left: auto;
  transition: transform .2s ease-in;
}

.primary_select .select_top .selected_value::-webkit-scrollbar {
  display: none;
}

.primary_select .select_bottom{
  display: none;
  background: #FFFFFF;
  position: absolute;
  top: calc(100% + 2px);
  left: 0;
  width: calc(100% + 10px);
  border: .0625rem solid rgba(140, 152, 164, .25);
  box-shadow: 0 0 1rem 0 rgba(140, 152, 164, .25);
  border-radius: 4px;
  min-height: 100px;
  max-height: 150px;
  overflow-y: auto;
  z-index: 99;
  padding: 6px;
}

.primary_select:has(.select_bottom.active) .select_top{
  color: #1e2022;
  background-color: #fff;
  border-color: rgba(140, 152, 164, .25);
  box-shadow: 0 0 1rem 0 rgba(140, 152, 164, .25);
}

.primary_select .select_bottom.active{
  display: block;
  animation: fade .1s ease-in;
}

.primary_select .select_bottom::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

.primary_select .select_bottom::-webkit-scrollbar-thumb  {
  background: #c1c1c1;
}

.primary_select .select_bottom::-webkit-scrollbar-track {
  background: #f1f1f1;
}

@keyframes fade{
  from {
      opacity: 0;
      transform: translateY(-10px);
  }
  to{
      opacity: 1;
      transform: translateY(0px);
  }
} 

.primary_select .select_bottom .option{
  cursor: pointer;
  padding: 7px 12px;
  color: #3D3D3B;
  transition: .1s linear;
  font-size: 14px;
  border-radius: 4px;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}

.primary_select .select_bottom .option .checked_icon{
  visibility: hidden;
}

.primary_select .select_bottom .option.selected .checked_icon{
  visibility: visible;
}

.primary_select .select_bottom .option:not(:first-child){
  margin-top: 3px;
}

.primary_select .select_bottom .option:hover, .primary_select .select_bottom .option.selected{
  background: #f5f5f5;
}

.edit_btn, .delete_btn{
  width: 22px; height: 22px;
  padding: 5px;
  border-radius: 4px;
  cursor: pointer;
}

.edit_btn {
  background: #d2e0f5;
}

.delete_btn{
  background : #f4b3b3;
}

.info {
  width: fit-content;
  position: relative;
}

.info .info_context{
  position: absolute;
  bottom:calc(100% + 3px);
  right: -28px;
  width: 180px;
  min-height: 100px;
  max-height: 150px;
  padding: 7px;
  background-color: #fff;
  border: 1px solid #4c6bbd;
  border-radius: 10px;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, .08);
  font-size: 11px;
  opacity: 0;
  transition: opacity .2s ease-out;
  visibility: hidden;
}

.info:hover .info_context{
  visibility: visible;
  opacity: 1;
}

.info .info_context::before{
  content: "";
  background: #fff;
  width: 6px;
  height: 6px;
  position: absolute;
  border-bottom: 1px solid #4c6bbd;
  border-right: 1px solid #4c6bbd;
  transform: rotate(45deg);
  bottom: -3.5px;
  right: 32px;
}

.skeleton {
	background-color: #e2e5e7;
	background-image: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));
	background-size: 40px 100%;
	background-repeat: no-repeat;
	background-position: left -40px top 0;
	animation: shine 1s ease infinite;
}

@keyframes shine {  
	to {
		background-position: right -40px top 0;
	}
}