.sidebar{
    margin-top: -60px;
    padding-top: 60px;
    position: fixed;
    box-shadow: 7px 0 60px rgba(0, 0, 0, .05);
    height: 100vh;
    z-index: 11;
}

.sidebar .sidebar_container{
    transition: width .1s ease-in-out;
}

.sidebar .sidebar_arrow{
    position: absolute;
    cursor: pointer;
    top: 3px;
    right: -10px;
    background: #6587dc;
    padding: 3px;
    border-radius: 10px;
    border: 2px solid #f1f4f6;
}

.sidebar .sidebar_arrow img{
    filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(336deg) brightness(108%) contrast(102%);
}

.sidebar_link{
    display: block;
    height: 40px;
    padding: 10px 1.5rem;
    cursor: pointer;
    transition: width .1s linear;
    position: relative;
}

.sidebar_link:has(.submenu)::after{
    content: '';
    position: absolute;
    opacity: 0;
    top: 25%;
    right: 0;
    width: 0; 
    height: 0; 
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent; 
    border-right:10px solid white; 
}

.sidebar_link:hover{
    background: #e0f3ff;
}

.sidebar_link.logout_link:hover{
    background : #f7e5ea;
}

.sidebar_link:hover::after{
    opacity: 1;
}

.sidebar_link:not(.logout_link):hover .link img{
    filter: brightness(0) saturate(100%) invert(40%) sepia(17%) saturate(4957%) hue-rotate(203deg) brightness(89%) contrast(89%);
}

.sidebar_link:not(.logout_link):hover .link span{
    color: #3f6ad8;
}

.sidebar_link .submenu{
    padding-left: 1px;
    width: 200px;
    position: absolute;
    top: 0;
    left: 100%; 
    display: none;
    flex-direction: column;
    background: #ffffff;
    max-height: 50vh;
    overflow: auto;
}

.sidebar_link:hover > .submenu{
    display: flex;
}

.sidebar_link .submenu a {
    position: relative;
    width: 100%;
    font-size: 12px;
    line-height: 18px;
    padding:8px 15px;
    color: #343a40;
    transition: all .1s ease-out;
}   

.sidebar_link .submenu > a:hover{
    color: #3f6ad8; 
    background: #e0f3ff;
}

.sidebar_link .submenu > a:not(:last-child){
    border-bottom: 1px solid #e5e7eb;
}

.logout_link {
    cursor: pointer;
    position: absolute;
    bottom: 15px;
    left: 0;
    display: flex;
    align-items: center;
    gap: .5rem;
    width: 100%;
}

.logout_link .link > img{
    filter: brightness(0) saturate(100%) invert(20%) sepia(65%) saturate(3538%) hue-rotate(330deg) brightness(96%) contrast(92%);
}


/* -------------------------------- */
.sidebar.mini .sidebar_arrow img{
    transform: rotate(180deg);
}

.sidebar.mini .sidebar_container{
    width: 60px;
}

.sidebar.mini .sidebar_container .sidebar_link{
    padding: 10px 20px !important;
}

.sidebar.mini .sidebar_container .link_text{
    display: none;
}

.sidebar.mini +.main_outer{
    padding-left: 60px;
}
